import React from 'react';
import Button from '../Buttons/Button';



const Form = props => {
	return (
		<section>
			<div className="container">
				<div className="info">
					<form className="form">
						<h3>Edit form:</h3>
							<p><label className="label1" >empName</label><input type="text" placeholder="" required></input></p>
							  <p><label className="label2">empActive</label><input type="checkbox" placeholder=""  required></input></p>
							    <p>
									<label className="label3">empDepartment</label>
									<select className="label-items" >
									<option>HR</option>
									<option>Tech</option>
									<option>Finans</option>
									</select>
							     </p>
								  <Button 
								    btn = "Save" 
								  />
									<Button 
									className="btn"
									
								    btn = "Cancel" 
								  />
								
					</form>
				</div>
			</div>
		</section>
	
	)
}

export default Form;
